<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>Settings</p>
        </div>
        <div class="orderDetailWrap">
            <div class="whiteBox flexBetween" style="height: 1.27rem;">
               <p class="itemLeft">Photo</p>
               <div class="itemRight flexStart">
                   <img class='userLogo' src="../assets/images/userLogo.png" alt="">
                   <img class='relultArrow' src="../assets/images/relultArrow.png" alt="" style="margin-left: .23rem;">
               </div>
            </div>
            <div class="whiteBox flexBetween" @click="clickNickName()">
               <p class="itemLeft">Name</p>
               <div class="itemRight flexStart">
                   <p class="rightText">{{userName ? userName : '--'}}</p>
                   <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
               </div>
            </div>
            <div class="whiteBox flexBetween">
               <p class="itemLeft">Mobile</p>
               <div class="itemRight flexStart">
                   <p class="rightText"></p>
                   <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
               </div>
            </div>
            <div class="whiteBox flexBetween">
               <p class="itemLeft">Password</p>
               <div class="itemRight flexStart">
                   <p class="rightText settingBlue"></p>
                   <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
               </div>
            </div>
            <div class="whiteBox flexBetween">
            <!-- <div class="whiteBox flexBetween" @click="goPage('/IdentityVerification')"> -->
               <p class="itemLeft">ldentity verification</p>
               <div class="itemRight flexStart">
                   <p class="rightText settingBlue"></p>
                   <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
               </div>
            </div>
            <div class="whiteBox flexBetween" @click='clickWithdrawSet()'>
               <p class="itemLeft">Withdrawral settings</p>
               <div class="itemRight flexStart">
                   <p class="rightText settingBlue">{{withdrawNumber ? '' : 'Not set'}}</p>
                   <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
               </div>
            </div>
            <div class="whiteBox flexBetween" @click="goPage('/About')">
               <p class="itemLeft">About YouLike</p>
               <div class="itemRight flexStart">
                   <img class='relultArrow' src="../assets/images/relultArrow.png" alt="">
               </div>
            </div>
            <div class="logoutBtn" style="margin-top: .72rem;" @click="logoutBox = true">Logout</div>
        </div>
        <van-dialog class='alertModelBox' v-model="logoutBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Log out
                    <img class='alertModelCloseIcon' @click="logoutBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">Do you want to quit now? </p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <div class="submitBtn_91 blueBorderBtn" style="width: 2.9rem;" @click="logoutBox = false">Cancel</div>
                        <div class="submitBtn_91 blueAllowBtn" style="width: 2.9rem;" @click="logout()" >Logout</div>
                    </div>
                </div>
            </template>
        </van-dialog>
        <van-dialog class='alertModelBox' v-model="changeNameBox" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Name
                    <img class='alertModelCloseIcon' @click="changeNameBox = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <div class="inputOuter flexBetween" style="margin-top: .26rem;">
                        <input class='normalInput' v-model="userName_change" type="email" maxlength="30" @input="changeInput(userName_change)">
                        <p class='errorMsgText' v-if="showUserNameAlert">{{userNameAlertText}}</p>
                    </div>
                    <div class="btnBox flexBetween" style="margin-top: .76rem;">
                        <div class="submitBtn_91 " :class='!showUserNameAlert ? "blueAllowBtn":""' style="width: 100%;" @click="changeUserName(userName_change)" >Save</div>
                    </div>
                </div>
            </template>
        </van-dialog>
        <van-dialog class='alertModelBox' v-model="notChangeAlert" :showConfirmButton='false'>
            <template #default>
                <div class="alertModelTitBox">
                    Note
                    <img class='alertModelCloseIcon' @click="notChangeAlert = false" style="" src="../assets/images/modelCloseIcon.png" alt="">
                </div>
                <div class="alertBoxMain">
                    <p class="alertContent">Name can only be changed once a day.<br>Please try again another day.</p>
                    <div class="btnBox flexBetween" style="margin-top: .57rem;">
                        <div class="submitBtn_91 blueAllowBtn" style="width: 100%;" @click="notChangeAlert = false" >OK</div>
                    </div>
                </div>
            </template>
        </van-dialog>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'Settings',
		data: function(){
			return {
				bgWrapHeight: 200,
                openStroe: true,
                moneySymbol: https.moneySymbol,
                logoutBox: false,
                withdrawNumber: '',
                userName: JSON.parse(localStorage.getItem('userMsg')).userName,
                notChangeAlert: false,

                changeNameBox: false,
                userName_change: '',
                showUserNameAlert: false,
                userNameAlertText: '',
                canGoSetting: true,
			}
		},
		created(){
			this.getWithdrawData()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getWithdrawData: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/pay/withdraw/status').then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        if(res.data.normal && res.data.normal.settings && res.data.normal.settings.length > 0) {
                            if(res.data.normal.settings[0].type == 'IFSC') {
                                this.withdrawNumber = res.data.normal.settings[0].IFSC.account
                            }
                            this.canGoSetting = true
                        }else if(res.data.normal && res.data.normal.settings && res.data.normal.settings.length == 0) {
                            this.withdrawNumber = false
                            this.canGoSetting = true
                        }else {
                            this.withdrawNumber = '1'
                            this.canGoSetting = false
                        }
						
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.withdrawNumber = ''
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getWithdrawData()
					}
				})
			},
            clickNickName(){
                this.userName_change = this.userName
                this.changeNameBox = true
            },
            changeUserName: function(userName_change){
                if(this.showUserNameAlert) {
                    return
                }
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    nickName: userName_change,
                }
				https.fetchPost('/api/user/profile/set/nickname',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.changeNameBox = false
                        this.userName = userName_change
					}else if(res.code == -1 && res.msg == 'Name can only be changed once a day.\nPlease try again another day.') {
                        this.changeNameBox = false
                        this.notChangeAlert = true
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.changeUserName()
					}
				})
			},
            logout: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchPost('/api/user/auth/logout').then((data) => {
					this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        localStorage.setItem('user-token','')
						this.goPage('/Home')
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.logout()
					}
				})
			},
            changeInput(name){
				let rge = /[<'>%*`?/&$\n^\s]/g;
				if(rge.test(name)){
					this.showUserNameAlert = true
					this.userNameAlertText = 'Cannot contain special symbols'
				}else if(!name || name.length < 4) {
					this.showUserNameAlert = true
					this.userNameAlertText = 'At least 4 characters'
				}else {
					this.showUserNameAlert = false
					this.userNameAlertText = ''
				}
			},
            clickWithdrawSet(){
                if(this.canGoSetting) {
                    this.goPage('/WithdrawalSettings')
                }
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
